.CreateTodoButton {
  background-color: #ffffff;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 50px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  color: #61DAFA;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 64px;
  padding-bottom: 10px;
  transform: rotate(0);
  transition: .3s ease;
  z-index: 9999;
}
.CreateTodoButton.rotated{
  transform: rotate(224deg);
}