.Modal-bg {
    background: rgba(34, 58, 67, 0.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #333333;
}
.Modal{
    width: 300px;
    min-height: 200px;
    background-color: #ffffff;
    border-radius: 20px;
    padding: 35px 20px;
    box-shadow: 1px 1px 20px #151515e8;
}

form {
    max-width: 90%;
    margin: 0 auto;
    display: flex;
    align-content: center;
    flex-direction: column;
    resize: none;
  }
  
  label {
    text-align: center;
    font-weight: bold;
    font-size: 25px;
    text-transform: uppercase;
    color: rgb(73, 223, 228);
    margin-bottom: 26px;
  }
  
  textarea {
    border-radius: 15px;
    border: 1px solid #eeeeee;
    box-shadow: 2px 2px 5px rgba(32, 35, 41, 0.30);
    color: #1E1E1F;
    font-size: 16px;
    text-align: left;
    padding: 15px;
    height: 60px;
    margin-bottom: 10px;
    width: calc(100%-25px);
  }
  
  textarea::placeholder {
  color: #a5a5a5;
  font-family: 'Oxygen', sans-serif;
  font-weight: 400;
  font-size: 16px;
  }
  
  textarea:focus {
    outline-color: #61DAFA;
  }
  
  .Form-btns {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  
  .Form-btn {
    cursor: pointer;
    display: inline-block;
    font-size: 16px;
    color: #ffffff;
    font-weight: 400;
    width: 120px;
    height: 40px;
    border-radius: 10px;
    border: none;
    font-family: 'Oxygen', sans-serif;
    box-shadow: 2px 2px 5px rgba(32, 35, 41, 0.30);
  }
  
  .Form-btn-add {
    background: rgb(245,120,218);
    background: linear-gradient(25deg, rgba(245,120,218,1) 0%, rgba(0,212,255,1) 100%);
  }
  .Form-btn-add:hover {
    background: rgb(209, 113, 188);
  }
  
  .Form-btn-cancel {
    background: rgb(0,212,255,1);
  }
  .Form-btn-cancel:hover {
    background: rgb(99, 160, 171);
  }