.LoadingTodoItem,
.ErrorTodoItem,
.EmptyTodoItem {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto 24px;
    box-shadow: 2px 2px 10px rgba(51, 51, 51, 0.3);
    border-radius: 15px;
    background: linear-gradient(90deg, rgba(250,250,250,1), rgb(218, 218, 218));
    background-size: 400% 400%;
    animation: loadingAnimation 3s ease-in-out infinite;
}
.TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

@keyframes loadingAnimation{
    0%{
        background-position: 0% 50%;
    }
    50%{
        background-position: 100% 50%;
    }
    100%{
        background-position: 0% 50%;
    }
}